footer {
	background: #070707;
	padding: 25px 0 15px;

	.col-lg-4 {
		border-right: 1px rgba(223, 223, 223, 0.1) solid;
		border-bottom: 1px rgba(223, 223, 223, 0.1) solid;

		&:nth-child(5),
		&:nth-child(6),
		&:nth-child(7) {
			border-bottom: 0;
			position: relative;
		}

		&:nth-child(4),
		&:nth-child(7) {
			border-right: 0;
		}

		&:nth-child(5),
		&:nth-child(6) {
			&:after {
				content: '';
				position: absolute;
				width: 80px;
				height: 50px;
				background: #070707;
				top: -25px;
				right: -40px;
			}
		}
	}

	.logo {
		max-width: 180px;
		margin-bottom: 10px;
	}

	.info-company {
		ul {
			list-style: none;
			padding: 0;

			li {
				// display: flex;
				margin-bottom: 12px;

				h5,
				span {
					font-size: 12px;
					color: #888888;
					display: inline;
				}

				h5 {
					font-weight: 700;
					display: inline;
					padding-right: 5px;
				}

				span {
					font-weight: 400;
				}

				&:last-child {
					margin-bottom: 30px;
				}
			}
		}
	}

	.list-footer {
		list-style: none;
		padding: 0;
		text-align: left;
		padding-left: 30px;

		li {
			margin-bottom: 15px;

			a {
				color: #888888;
				font-size: 12px;
				transition: .2s all ease-in-out;

				&:hover {
					color: #D82F25;
				}
			}

			@media (max-width: 768px) {
				margin-bottom: 10px;
			}
		}

		@media (max-width: 1024px) {
			padding-left: 0px;
		}
	}

	.coppy-right {
		padding-top: 15px;

		.box-img>a>img {
			max-width: 120px;
		}

		a {
			display: block;
			margin-top: 5px;
			font-size: 10px;
			color: #707070;
		}
	}

	.hot-line-mb {
		display: none;
		justify-content: center;
		align-items: center;
		height: 40px;
		font-size: 18px;
		font-weight: 700;
		color: #A3A3A3;
		background: #444444;
		border-radius: 5px;
		padding-top: 4px;
	}

	.hot-line {
		text-align: center;
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100%;

		h3 {
			color: #A3A3A3;
			font-size: 18px;
		}
	}

	.social-media {
		list-style: none;
		display: flex;
		padding: 0;
		justify-content: center;
		align-items: center;
		height: 100%;

		.item {
			margin-right: 35px;
		}

		&.mb {
			display: none;
		}
	}


	@media(max-width: 1024px) {
		padding: 25px 0px;

		.hot-line {
			display: none;
		}

		.hot-line-mb {
			display: flex;
		}

		.col-lg-4 {
			border: 0;

			&:after {
				display: none;
			}
		}

		.logo {

			img {
				display: block;
				margin: 0 auto;
			}
		}

		.info-company {
			ul {
				li {
					justify-content: center;

					&:last-child {
						margin-bottom: 20px;
						;
					}
				}
			}
		}

		.list-footer {
			margin-top: 20px;
			border-bottom: 1px rgba(223, 223, 223, 0.1) solid;
		}

		.social-media {
			display: none;

			&.mb {
				display: -webkit-inline-box;
				justify-content: space-between;
				margin-left: 15px;
				margin-bottom: 8px;
			}
		}

		.coppy-right {
			.box-img {
				overflow: hidden;
				display: flex;
				align-items: center;
			}
		}
	}
}