.about-5 {
	.row {
		justify-content: center;
		margin-bottom: -20px;
	}

	.col-lg-4 {
		margin-bottom: 20px;
	}

}

.about-project-item {
	position: relative;

	.imgbox {
		overflow: hidden;

		img {
			transform: scale(1.2);
			transition: .35s;
		}
	}

	figcaption {
		position: absolute;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 1;

		@media (min-width: 1025px) {
			padding: 60px;
			transition: .35s;
			opacity: 0;
			top: 0;
			left: 0;
			height: 100%;
		}

		@media (max-width: 1024.98px) {
			padding: 15px 25px;
			bottom: 0;
			background: rgba(black, .8);
		}

		h4 {
			color: white;
			font-size: 14px;
			font-weight: 400;

			@media (min-width: 1025px) {
				font-weight: 700;
				font-size: 18px;
				text-align: center;
				transition: .35s;
				transform: translateY(15px);
			}
		}
	}

	&:hover {
		.imgbox {
			img {
				transform: scale(1);
			}
		}

		figcaption {
			background: rgba(black, .8);
			opacity: 1;

			h4 {
				transform: translateY(0);
			}
		}
	}
}