.FaQ-page {
	.breadcrumb-wrapper {
		background: transparent;
	}
}

.FaQ {
	margin-top: 28px;
	margin-bottom: 50px;

	.title {
		font-size: 23px;
		color: #1C1C1C;
		text-decoration: underline;
	}

	.list-tabs-FaQ {
		margin-top: 30px;
		flex: 0 0 380px;
		max-width: 380px;

		.list-FaQ {
			background: #FFFFFF;
			border-radius: 5px;
			list-style-type: none;

			.item {
				cursor: pointer;
				font-size: 16px;
				color: #1C1C1C;
				padding: 10px 0 10px 30px;
				border-bottom: 1px solid #F5F5F5;

				&.active {
					color: #D82F25;
					border-bottom: 1px solid #D82F25;
				}
			}
		}
	}

	.content-FaQ {
		margin-top: 30px;
		margin-left: 17px;
		background: #FFFFFF;
		border-radius: 5px;
		padding: 20px 44px;

		.list-question {
			list-style-type: none;

			.item {
				position: relative;
				margin-bottom: 15px;
				padding-left: 17px;
				font-size: 14px;

				&:after {
					content: '';
					position: absolute;
					left: 0;
					top: 7px;
					width: 5px;
					height: 5px;
					border-radius: 50%;
					background: #1C1C1C;
				}

				&.active {
					color: #D82F25;

					&:after {
						background: #D82F25;
					}
				}
			}
		}

		.answer {
			margin: 15px 0 30px;
			padding: 30px;
			background: #F5F5F5;
			border-radius: 5px;
			box-shadow: 0 3px 6px rgba($color: #000000, $alpha: .1);

			h5 {
				font-size: 16px;
				color: #1C1C1C;
				font-weight: 700;
				margin-bottom: 20px;

				span {
					font-weight: 400;
				}
			}

			p {
				font-size: 16px;
				color: #888888;
				margin-bottom: 20px;

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}



	@media (max-width: 1024.98px) {
		margin-top: 25px;

		.title {
			text-align: center;
		}

		.list-tabs-FaQ {
			margin-top: 25px;
			flex: 0 0 100%;
			max-width: 100%;

			.list-FaQ {
				display: flex;
				flex-wrap: wrap;
			}
		}

		.content-FaQ {
			margin: 0;
			padding: 20px;

			.answer {
				padding: 15px;
			}
		}
	}

	@media (max-width: 575.98px) {
		.list-tabs-FaQ {
			.list-FaQ {
				.item {
					font-size: 12px;
				}
			}
		}

		.content-FaQ {
			.list-question {
				.item {
					font-size: 12px;
				}
			}

			.answer {
				h5 {
					font-size: 12px;

					span {
						font-weight: 400;
					}
				}

				p {
					font-size: 12px;
				}
			}
		}
	}
}